@import url('https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200;0,300;0,600;0,700;1,300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;
}

.navbar{
  background-color: #fff !important;
}

.navbar-brand{
  font-size: 1.5rem !important;
  color: #565387 !important;
}

.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}

.menu-active{
  font-weight: bold;
  border-bottom:  1px solid #535653;
}

.navbar a:hover{
  color: #3498db !important;
}

.brand-name{
  color: aqua;
}

.btn-get-startedhove {
  cursor: pointer;
  outline: none;
};

#header{
  width: 100%;
  height: 80px;
  margin-top: 70px;
}

#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 800px;
  color: black;
}

#header h2{
  color: black;
  font-size: 24px;
}

.animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@keyframes up-down {
  0%{
    transform: translateX(10px);
  }
   100% {
    transform: translateY(-10px);
  } 
}

@-webkit-keyframes up-down {
  0%{
    transform: translateX(10px);
  }
   100% {
    transform: translateY(-10px);
  } 
}

.card-title{
  font-weight: bold;
}

.footer-cls{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  background-color: #0d6efd;
  height: 70px;
  position:fixed;
  bottom:0px;
  left:0px;
  right:0px;
  margin-bottom:0px;
}

.footer-cls p{
  background: transparent;
  color: white;
}